import React, { useState, useRef, useEffect } from "react";
import "./ChatWidget.css";

const DEMO_RESPONSES = [
  "Hello! How can I help you today?",
  "I'm just a demo, but you can connect me to any AI backend!",
  "Try asking me anything about your portfolio.",
  "This chat UI is fully customizable!"
];

export default function ChatWidget() {
  const [open, setOpen] = useState(false);
  const [messages, setMessages] = useState([
    { from: "ai", text: DEMO_RESPONSES[0] }
  ]);
  const [input, setInput] = useState("");
  const messagesEndRef = useRef(null);

  useEffect(() => {
    if (open && messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
    }
  }, [messages, open]);

  const handleSend = (e) => {
    e.preventDefault();
    if (!input.trim()) return;
    setMessages((msgs) => {
      const newMsgs = [...msgs, { from: "user", text: input }];
      setTimeout(() => {
        const demoReply = DEMO_RESPONSES[(newMsgs.length) % DEMO_RESPONSES.length];
        setMessages((msgs2) => [
          ...msgs2,
          { from: "ai", text: demoReply }
        ]);
      }, 700);
      return newMsgs;
    });
    setInput("");
  };

  return (
    <>
      <div className={`chat-widget-fab${open ? " open" : ""}`} onClick={() => setOpen((o) => !o)}>
        <span role="img" aria-label="Chat">💬</span>
      </div>
      {open && (
        <div className="chat-widget-window">
          <div className="chat-widget-header">
            <span>AI Chat</span>
            <button className="chat-widget-close" onClick={() => setOpen(false)}>&times;</button>
          </div>
          <div className="chat-widget-messages">
            {messages.map((msg, i) => (
              <div key={i} className={`chat-widget-msg chat-widget-msg-${msg.from}`}>{msg.text}</div>
            ))}
            <div ref={messagesEndRef} />
          </div>
          <form className="chat-widget-inputbar" onSubmit={handleSend}>
            <input
              type="text"
              value={input}
              onChange={e => setInput(e.target.value)}
              placeholder="Type your message..."
              autoFocus
            />
            <button type="submit">Send</button>
          </form>
        </div>
      )}
    </>
  );
}
